import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PropertiesTableComponent } from './component/properties-table/properties-table.component';
import { PropertyDashboardComponent } from './component/property-dashboard/property-dashboard.component';

const routes: Routes = [
  { path: '', component: PropertiesTableComponent },
  { path: 'test', component: PropertyDashboardComponent },
  { path: '**', redirectTo: '' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
