import { Component, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';

import * as XLSX from 'xlsx';

@Component({
  selector: 'properties-table',
  styleUrls: ['properties-table.component.scss'],
  templateUrl: 'properties-table.component.html',
})
export class PropertiesTableComponent {
  displayedColumns: string[] = ['Address', 'City', 'State', 'Zip'];
  dataSource: MatTableDataSource<any>;
  hasBeenLoaded = false;
  sheet: string;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor() {
    const sheet = localStorage.getItem('excelSheet');
    if (sheet) {
      this.hasBeenLoaded = true;
      loadExcelToJSON(sheet, this);
    }
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  loadExcelSheet() {
    localStorage.setItem('excelSheet', this.sheet);
    this.hasBeenLoaded = true;
    loadExcelToJSON(this.sheet, this);
  }
}

function loadExcelToJSON(filePath: string, currentClass) {
  fetch(filePath, {
    mode: 'no-cors'
  })
    .then(res => {console.log('res', res); return res.blob()})
    .then(blob => {
      console.log('blob', blob);
      let workBook = null;
      let jsonData = null;
      const reader = new FileReader();
      reader.onload = (event) => {
        const data = reader.result;
        workBook = XLSX.read(data, { type: 'binary' });
        jsonData = workBook.SheetNames.reduce((initial, name) => {
          const sheet = workBook.Sheets[name];
          initial[name] = XLSX.utils.sheet_to_json(sheet);
          return initial;
        }, {});
        currentClass.dataSource = new MatTableDataSource(jsonData.Properties);
        currentClass.dataSource.paginator = currentClass.paginator;
        currentClass.dataSource.sort = currentClass.sort;
      }
      reader.readAsBinaryString(blob);
    })
}
